.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;

  @include tablet {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  transition: visibility .3s ease-in-out, opacity .5s linear;

  @include tablet {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }

  a {
    color: $blanco;
    text-decoration: none;
    font-size: 1.8rem;
    display: block;
    text-align: center;
    margin-right: 2rem;

    &:hover {
      color: $verde;
    }
  }

  @include tablet {
    margin-right: 2rem;
    font-size: 1.8rem;

    &:last-of-type {
      margin-right: 0;
    }
  }
}

/** Men√∫ de Hamburguesa **/
.mobile-menu {
  @include tablet {
    display: none;

  }

  img {
    width: 5rem;
  }
}

.mostrar {
  visibility: visible;
  opacity: 1;
}